                <div class="courses_list_radio_block" v-show="showCoursesDurationBlock">
                  <div class="courses_radio_item active">
                    <input type="radio" name="filter" id="8" class="courses_list_radio" checked />
                    <label for="8" class="radio_label">8</label>
                  </div>
                  <div class="courses_radio_item">
                    <input type="radio" name="filter" id="36" class="courses_list_radio" />
                    <label for="36" class="radio_label">36</label>
                  </div>
                  <div class="courses_radio_item">
                    <input type="radio" name="filter" id="40" class="courses_list_radio" />
                    <label for="40" class="radio_label">40</label>
                  </div>
                  <div class="courses_radio_item">
                    <input type="radio" name="filter" id="72" class="courses_list_radio" />
